const TASKS: Task[] = [
	new Task(1,"CLICK HERE!!","Add a new task by clicking 'Add Task' or click 'Edit' below to add a description and tick off once completed.",false,"16/02/17")
];

@Injectable()
export class TaskService  {

	getTasks() : Promise<Task[]> {
		return Promise.resolve(TASKS);
	}

	getTask(id: number) : Promise<Task> {
		return this.getTasks()
				.then(tasks => tasks.find(task => task.id === id))
	} 

}


