export class Task {
	id: number;
	stringId: string;
	name: string;
	desc: string;
	completed: boolean;
	dateCreated: string;

	constructor(id: number, name: string, desc: string, completed: boolean, dateCreated: string) {
		this.id = id;

		var temp = id.toString().length;
		this.stringId = id.toString();
		for (var i = 0; i < 7-temp; ++i) {
			this.stringId = this.stringId.concat("\xa0\xa0");
		}

		this.name = name;
		this.desc = desc;
		this.completed = completed;
		this.dateCreated = dateCreated;
	}
}


