import { Component } from '@angular/core';
import { OnInit }		from '@angular/core';
import { Router }		from '@angular/router';

import { Task } 	from './task';
import { TaskService } from './task.service';

@Component({
	moduleId: module.id,
	selector: 'todo-page',
	templateUrl: './todo.page.html',
	styleUrls: ['./todo.page.css'],
	providers: [TaskService]
})

export class TodoPage implements OnInit {
	title = "Todo List";
	tasks: Task[];

	selectedTask: Task;
	creationTask: Task;

	extendedState: boolean = true;

	count : number;

	creatingTask: boolean;

	constructor(private taskService: TaskService,
				private router: Router) {
	}

	ngOnInit() : void {
		this.taskService.getTasks()
			.then(tasks => this.tasks = tasks);
	}

	onSelect(task: Task) : void {
		this.selectedTask = task;
		this.extendedState = false;
	}

	onCreation() : void {
		this.creatingTask = true;
		var date: string = this.getDate();
		console.log(date);
		this.creationTask = new Task(this.tasks.length+1,"","",false,date);
	}

	getDate() : string {
		var today = new Date();
		var dd = today.getDate();
		var mm = today.getMonth()+1; // January is 0.
		var yyyy = today.getFullYear();

		var ddStr = dd.toString();
		var mmStr = mm.toString();

		if(dd < 10) {
		    ddStr="0"+dd;
		} 

		if(mm < 10) {
		    mmStr="0"+mm;
		} 

		return ddStr+"/"+mmStr+"/"+yyyy.toString().slice(2,4);
	}

	gotoDetail() : void {
		this.router.navigate(['/detail',this.selectedTask.id]);
	}

	addTask() : void {
		if(this.creationTask.name === '') {
			console.log("ERROR: name");
			this.creatingTask = false;
			alert("Please enter a task name.");
			return;
		}
		this.tasks.push(this.creationTask);
		this.creatingTask = false;
	}

	cancelAdd() : void {
		this.creatingTask = false;
	}

	closeDetail() : void {
		this.selectedTask = null;
		this.extendedState = true;
	}

}


